import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { redirectToMain } from '../../redux/actions';
import './BookDetails.scss';

const BookDetails = () => {
  const { book } = useSelector((state) => state);
  const dispatch = useDispatch();
  return (
    <div className="book-details">
      <div className="book-card_image-container">
        <img src={book.details.cover} alt={`cover of ${book.details.title}`} />
      </div>
      <div className="book-card-content">
        <h3 className="book-card_title">{`${book.details.title}`}</h3>
        <p className="book-card_subtitle">{`${book.details.author}`}</p>
      </div>
      <div className="book-card-footer">
        <span className="book-card_price">{`${book.details.price}$`}</span>
        <button
          className="book-card_button"
          type="button"
          onClick={() => {
            dispatch(redirectToMain());
          }}
        >
          Back To Catalog
        </button>
      </div>
    </div>
  );
};

export default BookDetails;
